import React, { Component } from 'react';
import { Row, Col, Form, Button, Input, Checkbox } from 'antd';

@Form.create({ name: 'formScholarshipApproval' })
class StructCodeForm extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const styleForm = {
      display: 'flex',
      flexWrap: 'wrap',
      alignItems: 'flex-end',
    };
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24} style={styleForm}>
          <Col span={6}>
            <Form.Item label="Độ dài của số tự tăng">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Bắt đầu đánh từ số">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phần đầu của mã">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phần cuối của mã">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Giải mã sinh viên sẽ là">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đến">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="">
              <Checkbox>Sinh viên chưa có mã</Checkbox>
            </Form.Item>
          </Col>
          <Col span={6}>
            <Row>
              <Col span={12}>
                <Form.Item label="">
                  <Button type="primary" htmlType="submit">
                    Lập mã tự động
                  </Button>
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="">
                  <Button type="danger">Xóa tất cả mã</Button>
                </Form.Item>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default StructCodeForm;



